.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    margin-bottom: 24px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;

    &:hover {
      color: $active-color;
    }
  }

  &__book-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $menu-text-color;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;

    &:hover {
      color: $active-color;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }
}
